import React, {Component} from 'react';
import CardListComponent from './component/card-list/card-list.component';
import {SearchBox} from './component/search-box/search-box.compoent';

import './App.css';

class App extends Component {

    monstersCopy = [];

    constructor(props) {
        super(props);
        console.log("constructor called -> ", props);
        this.state = {
            monsters: [],
            searchText: ''
        }
    }

    componentDidMount() {
        const monsters = [
            {
                name: "Monika Geller",
                img: "https://i.pinimg.com/736x/b7/6c/02/b76c0224d84a69096b75a627239256ed.jpg",
                occupation: "Chef",
                id: 1
            },
            {
                name: "Ross Geller",
                img : "data:image/jpeg;base64,",
                occupation: "Paleontologist",
                id: 2
            },
            {
                name: "Rachel Green Geller",
                img: "https://64.media.tumblr.com/48000cb75ef30827ad58bb2bc57bdc1e/tumblr_p9jyksXkvK1wl0f2ao7_250.jpg",
                occupation: "Waitress, Fashion Designer",
                id: 3
            },
            {
                name: "Chandler Bing",
                img: "data:image/jpeg;base64,",
                occupation: "Statistical Analysis and Data Reconfiguration",
                id: 4
            },
            {
                name: "Joey Tribbiani",
                img: "data:image/jpeg;base64,",
                occupation: "Actor",
                id: 5
            },
            {
                name: "Phoebe Buffay",
                img: "https://i.pinimg.com/236x/a3/99/8d/a3998d5b988c5a67e9449b2137f9c4b8.jpg",
                occupation: "Masseur, Singer",
                id: 6
            }
        ];
        this.setState({monsters: monsters, searchText: ''});
        this.monstersCopy = monsters;
    }

    filterMonsters(searchText) {
        console.log(searchText.value);
        const regex = new RegExp(searchText.value, 'gi');
        const filteredMonsters = this.monstersCopy.filter(
            monster => regex.test(monster.name)
        );
        this.setState({monster: filteredMonsters, searchText: searchText.value})
    }

    render() {
        const {monsters, searchText} = this.state;

        const filteredMonsters = monsters.filter(
          monster => monster.name.toLowerCase().includes(searchText.toLowerCase())
        );

        return (
            <div className="main">
                <h1 align="center">Monster App</h1>
                <SearchBox placeholder="Search For Monsters"
                           filterFunction={e => this.setState({monster: this.state.monsters, searchText: e.target.value})}
                />
                <CardListComponent monsters={filteredMonsters}/>
            </div>
        );
    }
}

export default App;
